import React, { useEffect, useRef } from 'react'
import Github from "../Images/Github.png"
import LinkedIn from "../Images/LinkedIn.png"
import Email from "../Images/Email.png";
function CSS1Text() {

  const Name = useRef()


    window.addEventListener("resize",()=>{
      const git = document.querySelector(".Github")
    const link = document.querySelector(".LinkedIn")
    const mail = document.querySelector(".Email")
      if (window.screen.width > 600) {
    Name.current.style.width = "40%"
      Name.current.style.height = "20%"
  
      Name.current.childNodes[1].style.opacity = "1"
      Name.current.childNodes[2].style.opacity = "1"
      Name.current.childNodes[3].style.opacity = "1"

        setTimeout(()=>{
          git.style.top = "120%"
          git.style.opacity = "1"
          link.style.top = "120%"
          link.style.opacity = "1"
          mail.style.top = "120%"
          mail.style.opacity = "1"
        },1)

    } else {
      Name.current.style.width = "80%"
      Name.current.style.height = "20%"
  
      Name.current.childNodes[1].style.opacity = "1"
      Name.current.childNodes[2].style.opacity = "1"
      Name.current.childNodes[3].style.opacity = "1"

      setTimeout(()=>{
          git.style.top = "120%"
          git.style.opacity = "1"
          link.style.top = "120%"
          link.style.opacity = "1"
          mail.style.top = "120%"
          mail.style.opacity = "1"
        },1)
    }
    })

    window.addEventListener("load",()=>{
      const git = document.querySelector(".Github")
    const link = document.querySelector(".LinkedIn")
    const mail = document.querySelector(".Email")
      if (window.screen.width > 600) {
    Name.current.style.width = "40%"
      Name.current.style.height = "20%"
  
      Name.current.childNodes[1].style.opacity = "1"
      Name.current.childNodes[2].style.opacity = "1"
      Name.current.childNodes[3].style.opacity = "1"

      setTimeout(()=>{
          git.style.top = "120%"
          git.style.opacity = "1"
          link.style.top = "120%"
          link.style.opacity = "1"
          mail.style.top = "120%"
          mail.style.opacity = "1"
        },3000)
    } else {
      Name.current.style.width = "80%"
      Name.current.style.height = "20%"
  
      Name.current.childNodes[1].style.opacity = "1"
      Name.current.childNodes[2].style.opacity = "1"
      Name.current.childNodes[3].style.opacity = "1"

      setTimeout(()=>{
          git.style.top = "120%"
          git.style.opacity = "1"
          link.style.top = "120%"
          link.style.opacity = "1"
          mail.style.top = "120%"
          mail.style.opacity = "1"
        },3000)
    }
    })
  

  return (
    <div className='Name'  ref={Name} >
        <div className="Background"></div>
        <div className="First">Jorge</div>
        <div className="Job">Frontend Developer</div>
        <div className="Last">San Juan</div>
        <a className='Github' target="_blank" href="https://github.com/Koke-1">
          <div className='GithubBack' ></div>
         <img src={Github} alt="" /> 
        </a>
        <a className='LinkedIn' target="_blank" href="https://www.linkedin.com/in/kokesanjuan/">
          <div className='LinkedInBack' ></div>
         <img src={LinkedIn} alt="" /> 
        </a>
        <a className='Email' target="_blank" href="mailto:sanjuangeorge@gmail.com">
          <div className='EmailBack' ></div>
          <img src={Email}/>
        </a>

    </div>
  )
}

export default CSS1Text