import React, { useRef } from 'react'
import Recipe from "../Images/Recipe.png"
import Tic from "../Images/Tic.png"

function CSS1Works() {
  const Work = useRef()
  
  setTimeout(()=>{
    Work.current.style.top = "80%"
    Work.current.style.opacity = 1
  },5000)


  return (
    <div className='Works' ref={Work} >
      <div className="WorksBack"></div>
        <h3 className="WorkTitle">Works</h3>
        <div className="Links">
        <a className='TicToc' target="_blank" href="https://github.com/Koke-1">
          <div className='TicBack' ></div>
         <img src={Recipe} alt="" /> 
        </a>
        <a className='Recipe' target="_blank" href="https://www.linkedin.com/in/kokesanjuan/">
          <div className='RecipeBack' ></div>
         <img src={Tic} alt="" /> 
        </a>
        </div>
    </div>
  )
}

export default CSS1Works